
import Badge from 'vux/src/components/badge/index.vue'

export default {
name: "MyTabHeader",
props: {
/**
* headerList格式
* badgeText传值则出现小红点， 不需要小红点则不需要传这个字段
* [{ name: '未分配', badgeText: 1 }]
*
*/
headerList: {
type: Array,
required: true
},

selectIndex: {
type: Number,
default: 0
}

},
components: {
Badge
},
data() {
return {
mySelectIndex: this.selectIndex,
moreIndex: null
}
},
methods: {
myChangeHeader(index) {
this.mySelectIndex = index

if (this.moreIndex === index) {
return false
}
this.$emit('changeHeader', index)
this.moreIndex = index
}
},
created() {
this.$emit('changeHeader', this.selectIndex)
},
computed: {
}

}
